ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

}

#featured img{
    width: 50vw;
    height: 50vh;
}

#categories {
    display: flex;
    justify-content: space-evenly;
}

.deals {
    display: flex;
    overflow-x: auto;
}




.products-list {
    display: flex;
    overflow-x: auto;
}

#mailing-list {
    text-align: center;
    color: black;
    height: 50vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;

}

#mailing-list button {
    width: 30%
}

.shop-all-btn {
    background: url(../../assets/images/mailinglist-graphic.jpeg);
    width: 100%;
    height: 100%;

    background-size: cover;
    transition: .4s ease;
    position: absolute;
    top: 0;
    left: 0;
}

.shop-all-btn:hover {
    transform: scale(1.3);
}

.shop-all-container p {
    font-weight: bold;
    font-size: 26px;
    position: absolute;
    z-index: 2;


}

#seasonal-picks{
    width: 100vw;
    height: 96vh;
    overflow: hidden;
}
.banner {
    height: 50vh;
    background-color: var(--accent-color);
}
.seasonal-grid button{
    z-index: 10;

    background-size: cover;
    transition: .4s ease;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.seasonal-grid p {
    position: absolute;
    z-index: 10;
    color: white;
    font-size: 2rem;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.seasonal-grid::after {
    content: "";
    width: 100%;
    position: absolute;
    top: 0;
    z-index: 9;
    left:0;
    opacity: 0.4;
    height: 100%;
    background-color: #000000fc;

}
