.seasonal-grid {
    display: grid;
    grid-template-areas:
      "left middle-top right"
      "left middle-bottom right";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 10px; /* Adjust spacing between images */
    height: 100%; /* Ensure the grid spans the full height */
}
.shop-all-container p:hover+.shop-all-btn {
    transform: scale(2);
}

.shop-all-container {
    display: flex;
    overflow: hidden;
    min-width: 150px;
    color: white;

    min-height: 150px;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: black;

}

.new-arrival-item {

    height: 150px;
    width: 150px;
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    position: relative;
}

#mailing-list h2 {
    background-image: url(../../assets/images/mailinglist-graphic.jpeg);
    height: 90px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: 40%;
    width: 100%;
    background-repeat: no-repeat;
    font-family: Dunbar;
    font-weight: bold;
    background-color: black;
}

#mailing-list p {

    color: darkgray;
    margin-top: 20px;
    width: 50%
}

#mailing-list button {
    margin-top: 30px;

}

.new-arrivals-scroll {
    display: flex;
    overflow-x: auto;
    align-items: center;
    height: 220px
}

.new-arrival-item img {
    width: 120px;
    height: auto;
}

.new-arrival-item {
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: center;
    margin: 0 20px;
    width: 200px;

    padding: 10px;
}

.new-arrival-item button {
    color: #00000070;
    text-decoration: underline;
    text-align: center;
    position: absolute;
    bottom: 2px;
}

@media only screen and (max-width: 580px) {
    #categories {
        flex-direction: column;
        align-items: center;
    }

    .shop-all-container {
        min-width: 160px;
        min-height: 160px
    }

    .new-arrival-item {

        min-width: 150px;
        height: 160px;
    }

    .new-arrival-item img {
        width: 100px;
        height: 100px
    }





}