.product-measurements .heading {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    justify-content: space-between;


}

.product-measurements h3 {
    margin-bottom: 20px;
}

.in,
.cm {
    padding: 5px 15px;
    border: 2px solid #00000020;

}

.how-to-measure li span {
    font-weight: bold;

}

ol {
    list-style: none;
    counter-reset: item;
}

ol li {
    counter-increment: item;
    margin-bottom: 5px;
}

ol li:before {
    margin-right: 10px;
    content: counter(item);
    background: var(--second-bg-color);
    border-radius: 100%;
    color: white;
    width: 1.2em;
    text-align: center;
    display: inline-block;
}

.how-to-measure img {
    width: 300px;
    height: auto
}

.how-to-measure li {
    font-family: 'Fredoka', sans-serif;
}

.how-to-measure {
    align-items: center;
    margin: 0 30px;
    display: flex;
    justify-content: space-evenly;
}

.custom-select {
    min-width: 100px;
    position: relative;
    margin-right: 20px;
}


.custom-select::before,
.custom-select::after {
    --size: 0.3rem;
    position: absolute;
    content: "";
    right: 8px;
    pointer-events: none;
}

.custom-select::before {
    border-left: var(--size) solid transparent;
    border-right: var(--size) solid transparent;
    border-bottom: var(--size) solid var(--primary-color);
    top: 40%;
}

.custom-select::after {
    border-left: var(--size) solid transparent;
    border-right: var(--size) solid transparent;
    border-top: var(--size) solid var(--primary-color);
    top: 55%;
}

.settings select:hover {
    border-color: var(--primary-color)
}



.settings select {
    appearance: none;
    /* safari */
    -webkit-appearance: none;
    /* other styles for aesthetics */
    width: 100%;

    font-size: 1.15rem;
    padding: 10px 20px;
    background-color: #fff;
    border: 2px solid #00000020;
    border-radius: 0.25rem;
    color: #000;
    border-radius: 25px;
    cursor: pointer;
}

.settings {
    display: flex;
}


.heading .in {
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.heading .cm {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-measurements .heading .active-unit {
    background-color: var(--primary-color);
    color: white;
    border: none;
}

@media only screen and (max-width: 768px) {

    .how-to-measure {

        flex-direction: column-reverse;
    }

    .product-measurements .heading {
        flex-direction: column;
        text-align: left;
        align-items: flex-start;
    }

    .cm,
    .in {
        font-size: 90%;
    }


    .settings {
        justify-content: space-between;
        display: flex;
    }


}